import React, { useState } from 'react'
import './homeslide.css'
import HomeSlideData from '../../Assets/HomeSlideData'
import ArrowBackIosNewIcon from '@mui/icons-material/ArrowBackIosNew';
import ArrowForwardIosIcon from '@mui/icons-material/ArrowForwardIos';

const HomeSlide = () => {

  const [slideNum, setSlide] = useState(1);

  const [slideImage, setImage] = useState(HomeSlideData[0].img)

  const handleImageback = () => slideNum > 1 && setSlide(prevalue => {
    HomeSlideData.map(item => item._id === (prevalue - 1) && setImage(item.img));
    return (prevalue - 1)
  })

  const handleImageforward = () =>
    slideNum < 5 && setSlide(prevalue => {
      HomeSlideData.map(item => item._id === (prevalue + 1) && setImage(item.img));
      return (prevalue + 1)
    });

  return (
    <div>
      <div className='slide-container' >
        <button onClick={() => { handleImageback() }}><ArrowBackIosNewIcon /></button>
        <div style={{ backgroundImage: `url(${slideImage})` }}></div>
        <button onClick={() => { handleImageforward() }}><ArrowForwardIosIcon /></button>
      </div>
    </div>
  )
}

export default HomeSlide

