import React from 'react'
import { Link } from 'react-router-dom'
import './footer.css'
import InstagramIcon from '@mui/icons-material/Instagram';
import XIcon from '@mui/icons-material/X';
import FacebookIcon from '@mui/icons-material/Facebook';
const Footer = () => {
    return (
        <div className='footer'>
        <div>
            <h2>Happy Shopping!</h2>
            <p className='info'>Become a part of our growing community of savvy shoppers! Connect with us on social media, share your Amazon finds, and stay updated on the latest trends and deals.</p>
            </div>
            <div className='footer-right'>
            <div className='explore'>
                <h2>Explore</h2>
                <ul>
                    <li><Link to="/">Home</Link></li>
                    <li><Link to="/catagories">Catagories</Link></li>
                    <li><Link to="/blogs">Blogs</Link></li>
                    <li><Link to="/about">About</Link></li>
                </ul>
            </div>
            <div className='follow'>
                <h2>Follow</h2>
                <ul>
                <li><Link to="/"><InstagramIcon/>Instagram</Link></li>
                <li><Link to="/"><XIcon/>X</Link></li>
                <li><Link to="/"><FacebookIcon/>Facebook</Link></li>
                </ul>
            </div>
            </div>
        </div>
    )
}

export default Footer
