import React,{useState,useEffect} from 'react';
import { BrowserRouter, Routes, Route } from 'react-router-dom';
import './App.css';
import Navbar from './Components/Navbar/Navbar';
import Home from './pages/Home';
import Catagories from './pages/Catagories';
import LoginSignup from './pages/LoginSignup';
import Blogs from './pages/Blogs';
import About from './pages/About';
import Footer from './Components/Footer/Footer';
import SingleBlog from './Components/SingleBlog/SingleBlog';
import Cart from './Components/Cart/Cart';


function App() {

  return (
    
    <BrowserRouter>
      <Navbar />
      <Routes>
      <Route path='/cart' element={<Cart/>}/>
        <Route path='/' element={<Home />} />
        <Route path='/ecommerce' element={<Home />} />

        <Route path='/catagories' element={<Catagories catagory='mobiles' reset="yes" />} />
        <Route path='/catagories/mobiles' element={<Catagories catagory="Mobile" reset={12} />} />
        <Route path='/catagories/television' element={<Catagories catagory="TV" reset={12} />} />
        <Route path='/catagories/laptops' element={<Catagories catagory="Laptop" />} />

        <Route path='/catagories/kitchen' element={<Catagories catagory="Kitchen" />} />
        <Route path='/catagories/books' element={<Catagories catagory="Book" />} />
        <Route path='/catagories/shoes' element={<Catagories catagory="Shoe" />} />
        <Route path='/catagories/other' element={<Catagories catagory="Other" />} />

        <Route path='/blogs' element={<Blogs />} />
        <Route path='/blog' element={<SingleBlog />}>
          <Route path=':blogId' element={<SingleBlog />} />
        </Route>

        <Route path='about' element={<About />} />

        <Route path='/login' element={<LoginSignup />} />
      </Routes>
      <Footer />
    </BrowserRouter>
  );
}

export default App;
