import React from 'react'
import HomeSlide from '../Components/HomeSlide/HomeSlide';
import TrendSlide from '../Components/Trending/TrendSlide';
import TrendingData from '../Assets/TrendingOffersdata';
import KitchenTrend from '../Assets/KitchenTrend';
import HomeCircleTrend from '../Components/HomeCircleTrend/HomeCircleTrend';
const Home = () => {
  return (
    <div onLoad={window.scroll(0,0)}>
      <HomeSlide/>
      <HomeCircleTrend/>
      <TrendSlide title='Trending Offers' data={TrendingData}/>
      <TrendSlide title='Best in Mobiles' data={TrendingData}/>
      <TrendSlide title='Exclusive Offers in TVs' data={TrendingData}/>
      <TrendSlide title='Trending in Laptops' data={TrendingData}/>
      <TrendSlide title='Best in Electronics' data={TrendingData}/>
      <TrendSlide title='Top Deal in Kitchen' data={KitchenTrend}/>
      <TrendSlide title='Deals in shoes' data={TrendingData}/>
    </div>
  )
}

export default Home;
