import React, { useContext } from 'react'
import Item from '../Item/Item';
import CatagoriesMenu from '../CatagoriesMenu/CatagoriesMenu';
import { useState } from 'react';
import './catagory.css'
import { ShopContext } from '../../ShopContext/ShopContext';

const Catagory = (props) => {
  const {all_products} = useContext(ShopContext);
  const [productNum, setProductNum] = useState(props.reset);
  let count = 1;

  return (
    <div>
    <div className='catagory-container' >
      <CatagoriesMenu setProductNum={setProductNum}/>
      <div className='catagory-products'>
        {all_products.filter(product => {
          if (count <= productNum && product.catagory === props.catagory) {
            count++;
            return (true);
          }
          else {
            return false
          }
          
        }
        ).map(item => {
          return <Item img={item.img} product_name={item.product_name} old_price={item.old_price} new_price={item.new_price} discount={item.discount} url={item.url} />
        }
        )}
      </div>
    </div>
    <button className='see-more' onClick={() => setProductNum(pre => pre + 12)} >See More..</button>
    </div>
  )
}

export default Catagory;
