import React from 'react'
import { Link } from 'react-router-dom'
import './catagoriesmenu.css'
const CatagoriesMenu = (props) => {
  
  return (
    <div className='catagoriesmenu'>
      <ul>
        <Link to='/catagories/mobiles' onClick={()=> props.setProductNum(12)}><li>Mobiles</li></Link>
        <Link to='/catagories/television' onClick={()=> props.setProductNum(12)}><li>TVs</li></Link>
        <Link to='/catagories/laptops' onClick={()=> props.setProductNum(12)}><li>Laptops</li></Link>
        <Link to='/catagories/kitchen' onClick={()=> props.setProductNum(12)}><li>Kitchen</li></Link>
        <Link to='/catagories/shoes' onClick={()=> props.setProductNum(12)}><li>Shoes</li></Link>
        <Link to='/catagories/books' onClick={()=> props.setProductNum(12)}><li>Books</li></Link>
        <Link to='/catagories/other' onClick={()=> props.setProductNum(12)}><li>Other</li></Link>
      </ul>
    </div>
  )
}

export default CatagoriesMenu
