import React, {useState, useContext} from 'react'
import './blogDisplay.css'
import BlogItem from '../BlogItem/BlogItem'
import { ShopContext } from '../../ShopContext/ShopContext'
const BlogDisplay = () => {
    const [blogNum, setBlogNum] = useState(9);
    let count = 1;
    const {all_blogs} = useContext(ShopContext)
    
    return (
        <div className='blog-container'>
            <div className='blog-top'>
                <div className='blog-top-1'>
                     {all_blogs.slice(-1).map(item => <BlogItem img={item.img_url} title={item.title} content={item.content} top={true} id={item.id}/>)} 
                </div>
                <div className='blog-top-2'>
                    <div>
                        {all_blogs.filter(blog => blog.id === all_blogs.length - 1).map(item => <BlogItem img={item.img_url} title={item.title} content={item.content} id={item.id}/>)}
                    </div>
                    <div>
                        {all_blogs.filter(blog => blog.id === all_blogs.length - 2).map(item => <BlogItem img={item.img_url} title={item.title} content={item.content} id={item.id}/>)}
                    </div>
                </div>
            </div>
            <div className='blog-bottom'>
                {all_blogs.filter(blog =>{ 
                    if(blog.id < all_blogs.length - 2 && count<=blogNum){
                        count++;
                        return true;
                    }
                    else{
                        return false;
                    }}).map(item => <BlogItem img={item.img_url} title={item.title} content={item.content} id={item.id}/>)}
            </div>
            <button className='see-more' onClick={() => setBlogNum(pre => pre + 9)}>See More..</button>
        </div>
    )
}

export default BlogDisplay
