import React, { useState } from 'react'
import './trendslide.css'
import Item from '../Item/Item'
import ArrowBackIosNewIcon from '@mui/icons-material/ArrowBackIosNew';
import ArrowForwardIosIcon from '@mui/icons-material/ArrowForwardIos';

const TrendSlide = (props) => {

  const [trendnum, setTrendNum] = useState(5);
  const nextTrend = ()=> trendnum===5&&setTrendNum(pre=>pre+5)
  const prevTrend = ()=> trendnum===10&&setTrendNum(pre=>pre-5)
  return (
    <div className='Trend'>
    <h2>{props.title}</h2>
    <div className='Trend-container'>
    <button onClick={prevTrend}><ArrowBackIosNewIcon fontSize="large"/></button>
        {props.data.filter(item=>item.id<=trendnum && item.id>trendnum-5).map(item=>
            <Item img={item.img} product_name={item.product_name} old_price={item.old_price} new_price={item.new_price} discount={item.discount} url={item.url}/> 
        )}
      <button onClick={nextTrend}><ArrowForwardIosIcon fontSize="large"/></button>
    </div>
    </div>
  )
}

export default TrendSlide;
