import { createContext, useEffect, useState } from "react";
export const ShopContext = createContext();

// import("dotenv/config");

const ShopContextProvider = (props) => {
    const [all_products, setAllProducts] = useState([]);
    const fetchProducts = async () => {
        await fetch(`${process.env.REACT_APP_URL}/allproducts`).then(res => res.json()).then(data => setAllProducts(data));
    }

    const [all_blogs, setAllBlogs] = useState([]);
    const fetchBlogs = async () => {
        await fetch(`${process.env.REACT_APP_URL}/allblogs`).then(res => res.json()).then(data => setAllBlogs(data));
    }
    useEffect(() => {
        fetchProducts();
        fetchBlogs();
    }, [])

    return (
        <ShopContext.Provider value={{ all_products, all_blogs }}>
            {props.children}
        </ShopContext.Provider>
    )
}

export default ShopContextProvider;