import React from 'react'
import './homecircletrend.css'
import { Link } from 'react-router-dom'
const HomeCircleTrend = () => {
  return (
    <div className='circle-trend'>
      <div><Link to='catagories/mobiles'><div className='circle-trend-item'><img src='https://m.media-amazon.com/images/I/719PYirSevL._AC_UY327_FMwebp_QL65_.jpg' alt='' /></div></Link><p>Mobiles</p></div>
      <div><Link to='catagories/television'><div className='circle-trend-item'><img src='https://m.media-amazon.com/images/I/71S8iUPW9bL._AC_UY327_FMwebp_QL65_.jpg' alt=''/></div></Link><p>TVs</p></div>
      <div><Link to='catagories/laptops'><div className='circle-trend-item'><img src='https://m.media-amazon.com/images/I/616TNXcZcHL._AC_UY327_FMwebp_QL65_.jpg' alt='' /></div></Link><p>Laptops</p></div>
      <div><Link to='catagories/shoes'><div className='circle-trend-item'><img src='https://m.media-amazon.com/images/I/614jguug7SL._AC_UL480_FMwebp_QL65_.jpg' alt='' /></div></Link><p>Shoes</p></div>
      <div><Link to='catagories/kitchen'><div className='circle-trend-item'><img src='https://m.media-amazon.com/images/I/810IjwSOWZL._AC_UL480_FMwebp_QL65_.jpg' alt=''  /></div></Link><p>Kitchen</p></div>

    </div>
  )
}

export default HomeCircleTrend
