import React, {useRef, useState} from "react";
import {logo, profile} from "../../Assets/image";
import './Navbar.css'
import { Link } from "react-router-dom";
import MenuIcon from '@mui/icons-material/Menu';
import LocalGroceryStoreIcon from '@mui/icons-material/LocalGroceryStore';
const Navbar = () => {
    const [menu, setmenu] = useState("Home");

    const menuRef = useRef();

    const dropdown_toggle = (e) =>{
      menuRef.current.classList.toggle('nav-menu-visible');
      e.target.classList.toggle('open');
    }

  return (
    <div className="navbar">
      <div className="logo-container">
      <div className='nav-dropdown' onClick={dropdown_toggle}>
        <MenuIcon/>
        </div>
        <img src={logo} alt="#"></img>
        <p>All in One</p>
      </div>
        <ul ref={menuRef} className="menu">
            <li><Link to="/" onClick={()=>{setmenu("Home")}}>Home</Link>{menu==="Home"?<hr/>:<></>}</li>
            <li><Link to="/catagories/mobiles"  onClick={()=>{setmenu("Catagories")}}>Catagories</Link>{menu==="Catagories"?<hr/>:<></>}</li>
            <li><Link to="/blogs"  onClick={()=>{setmenu("Blogs")}}>Blogs</Link>{menu==="Blogs"?<hr/>:<></>}</li>
            <li><Link to="/about"  onClick={()=>{setmenu("Best")}}>About</Link>{menu==="Best"?<hr/>:<></>}</li>
        </ul>
      <div className="profile">
      <Link to="/login"><button>Login</button></Link>
      <Link to="/cart"><LocalGroceryStoreIcon/></Link>
        <img src={profile} alt="#"></img>
      </div>
    </div>
  )
}

export default Navbar;
