import React from 'react'
import './item.css'
import { Link } from 'react-router-dom'
const Item = (props) => {
  return (
    <div className='item'>
    <p className='item-discount'>{props.discount}</p>
      <div className='item-img-container'>
        <img src={props.img} alt=''></img>
      </div>
      <hr/>
      <p className='product_name'>{props.product_name.substring(0, 25)}...</p>
      <div className='item-bottom'>
        <div>
          <p className='old_price'>{props.old_price}</p>
          <p className='new_price'>{props.new_price}</p>
        </div>
        <button><Link to={props.url}>Get Deal</Link></button>
      </div>
    </div>
  )
}

export default Item
