import React,{useState, useEffect, useContext} from 'react'
import './singleBlog.css'
import { useParams } from 'react-router-dom'
import blogData from '../../Assets/BlogData'
import Breadcrum from '../Breadcrum/Breadcrum'
import { ShopContext } from '../../ShopContext/ShopContext'
import parse from 'html-react-parser';

const SingleBlog = () => {
  const {all_blogs} = useContext(ShopContext);
  const { blogId } = useParams();
  all_blogs.filter(e => e.id === Number(blogId));


  return (
    all_blogs.filter(e => e.id === Number(blogId)).map(blog=>
    <div className='single-blog-container' onLoad={window.scroll(0,0)}>
      <Breadcrum  title={blog.title} />
      <div className='single-blogitem'>
        <img src={blog.img_url} alt=''></img>
        <h2>{blog.title}</h2>
        <div>{parse(blog.content)}</div>
      </div>
    </div>
    )
  )
}

export default SingleBlog
